.input {
    border-radius: 5px;
    width: 300px;
    height: 40px;
    font-size: 22px;
    margin-bottom: 10px;
    padding: 5px;
    border: 1px solid rgba(57, 55, 55, 0.96);
}

.triangle {
    width: 0;
    height: 0;
    border-left: 7px solid transparent;
    border-right: 7px solid transparent;
    border-bottom: 10px solid red;
    margin-top: -10px;
}

.errorMessage {
    font-weight: bold;
    display: inline-block;
    padding: 3px 20px 3px 20px;
    background-color: red;
    margin-bottom: 10px;
    border-radius: 3px;
    margin-top: -17px;
}

.error {
    border: 2px solid red;
}

.exclamation {
    margin-left: 5px;
    font-size: 20px;
    color: red;
}